import { Component, OnInit, HostListener } from '@angular/core';
import Chart from "chart.js";


@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
})


export class HomeComponent implements OnInit {

  constructor() { }

  ngOnInit(): void {

    let body = document.getElementsByTagName("body")[0];
    body.classList.add("home-page");
    let easterStep = 0;

    let canvas: any = document.getElementById("chartBig");
    let ctx = canvas.getContext("2d");
    let gradientFill = ctx.createLinearGradient(0, 350, 0, 50);
    gradientFill.addColorStop(0, "rgba(228, 76, 196, 0.0)");
    gradientFill.addColorStop(1, "rgba(228, 76, 196, 0.14)");
    let chartBig = new Chart(ctx, {
      type: "line",
      responsive: true,
      data: {
        labels: [
          "2009",
          "2010",
          "2011",
          "2012",
          "2013",
          "2014",
          "2015",
          "2016",
          "2017",
          "2018",
          "2019",
          "2020",
          "2021"
        ],
        datasets: [
          {
            label: "Node.js",
            fill: true,
            backgroundColor: "transparent",
            borderColor: "white",
            borderWidth: 2,
            borderDash: [],
            borderDashOffset: 0.0,
            pointBackgroundColor: "white",
            pointBorderColor: "rgba(255,255,255,0)",
            pointHoverBackgroundColor: "black",
            //pointHoverBorderColor:'rgba(35,46,55,1)',
            pointBorderWidth: 20,
            pointHoverRadius: 4,
            pointHoverBorderWidth: 15,
            pointRadius: 4,
            data: [0, 16, 21, 33, 44, 46, 51, 52, 58, 61, 76, 81, 93]
          }
        ]
      },
      options: {
        maintainAspectRatio: false,
        legend: {
          display: false
        },

        tooltips: {
          backgroundColor: "#8a00f2",
          titleFontColor: "#fff",
          bodyFontColor: "#fff",
          bodySpacing: 4,
          xPadding: 12,
          mode: "nearest",
          intersect: 0,
          position: "nearest"
        },
        responsive: true,
        scales: {
          yAxes: [
            {
              barPercentage: 2.6,
              gridLines: {
                drawBorder: false,
                color: "rgba(0,0,0,0.0)",
                zeroLineColor: "transparent"
              },
              ticks: {
                display: false,
                suggestedMin: 0,
                suggestedMax: 350,
                padding: 20,
                fontColor: "#9a9a9a"
              }
            }
          ],

          xAxes: [
            {
              barPercentage: 1.6,
              gridLines: {
                drawBorder: false,
                color: "rgba(0,0,0,0)",
                zeroLineColor: "transparent"
              },
              ticks: {
                padding: 20,
                fontColor: "white"
              }
            }
          ]
        }
      }
    });

    let audioProgress = document.querySelector('.audioProgress .progress-circle');

    let Fuzil = document.querySelector('.random-trash-music');
    let FuzilText = document.querySelector('.random-trash-music p');
    let easterEgg = document.querySelector('.easter-egg');
    let easterEgg2 = document.querySelector('.easter-egg2');
    let stopElement = document.querySelector('.stopthisfsong');


    function ProgressTime(audioInside){
      audioInside.addEventListener('timeupdate', (event) => {

        const currentTime = Math.floor(audioInside.currentTime);
        const duration = Math.floor(audioInside.duration);

        let audioP = Math.round((currentTime*100)/duration);
        let audioString = `p${Math.round((currentTime*100)/duration)}`;
        //let audioRegex = `p${Math.round((currentTime*100)/duration)}`;
        let replaceRegex = new RegExp(`(?:^|\\s)${audioProgress.classList[1]}(?!\\S)`,'g');

        if(audioP > 50 ){
          let audioStringF = `p${Math.round((currentTime*100)/duration)}`;

          audioProgress.classList.add('over50');

          audioProgress.className = audioProgress.className.replace( replaceRegex, ` ${audioStringF}`); 

        } else {
          audioProgress.className = audioProgress.className.replace( replaceRegex, ` ${audioString}`); 
        }

        if(audioP == 100){
          setTimeout(() => {
            audioProgress.className = 'progress-circle p0';
          }, 500);
        }

      }, true);

      return audioInside;
    }

    easterEgg2.addEventListener('click', e => {

      if(easterStep == 0){
        easterEgg2.classList.remove('active');
        easterEgg2.classList.remove('playing');
      }

      easterStep ++;

      if(easterStep == 1){
        easterEgg2.classList.add('active');
      }
      
      if(easterStep == 2){

        let cidm = new Audio("assets/sample/dcitm.mp3");
        easterEgg2.classList.add('playing');

        cidm.volume = 0.06;
        cidm.play();
  

        ProgressTime(cidm);

        stopElement.classList.remove('hide');

        stopElement.addEventListener('click', e => {

          let autoGen = document.querySelector('.easter-egg2');

          cidm.pause();
          cidm.currentTime = 0;
          audioProgress.className = 'progress-circle p0';

          easterEgg2.classList.remove('playing');
          // console.log(autoGen.classList);
          autoGen.classList.remove('active');
          autoGen.classList.add('lock-easter');

          stopElement.classList.add('hide');

          easterStep = 0;


          e.stopImmediatePropagation();

        });

        cidm.addEventListener('ended', (event) => {

          easterEgg2.classList.remove('playing');
          easterEgg2.classList.remove('active');
          easterEgg2.classList.add('lock-easter');
          stopElement.classList.add('hide');

          audioProgress.className = 'progress-circle p0';

        });

        //console.log(easterStep);

      }




    });

    let randomMusic;

    easterEgg.addEventListener('click', e => {

      Fuzil.classList.add('show');
      Fuzil.classList.remove('hide');

      randomMusic = Math.round(Math.random() * 11);

      //console.log(typeof(randomMusic), randomMusic, randomMusic >= 0 && randomMusic < 3);

      if(randomMusic >= 0 && randomMusic <= 3){

        let papumText = "exchanged my ps for a...";
        FuzilText.innerHTML = papumText;

      } else if (randomMusic >= 4 && randomMusic <= 6){

        let tranquilebaText = "come quietleba go caralho";
        FuzilText.innerHTML = tranquilebaText;

      }  else {

        let badassText = "A badass music";
        FuzilText.innerHTML = badassText;

      }

    });

    Fuzil.addEventListener('click', e => {

      //console.log('Fuzil:', typeof(randomMusic), randomMusic, randomMusic >= 0 && randomMusic < 3);

      if(randomMusic >= 0 && randomMusic <= 3){

          let papum = new Audio('assets/sample/exchanged-ps.mp3');
          papum.volume = 0.06;
          papum.play();

          // papum.addEventListener('ended', e => {
          //   console.log('acabou');
          // });

          ProgressTime(papum);


          easterEgg.addEventListener('click', e => {
            papum.pause();
            papum.currentTime = 0;
            audioProgress.className = 'progress-circle p0';

            setTimeout(() => {
              Fuzil.classList.remove('show');
              Fuzil.classList.add('hide');
            }, 6000);
          });

      } else if (randomMusic >= 4 && randomMusic <= 6){

          let tranquileba = new Audio('assets/sample/vem-tranquileba.mp3');
          tranquileba.volume = 0.06;
          tranquileba.play();

          ProgressTime(tranquileba);

          easterEgg.addEventListener('click', e => {
            tranquileba.pause();
            tranquileba.currentTime = 0;
            audioProgress.className = 'progress-circle p0';

            setTimeout(() => {
              Fuzil.classList.remove('show');
              Fuzil.classList.add('hide');
            }, 6000);
          });

      } else {

          let badass = new Audio('assets/sample/a-badass.mp3');
          badass.volume = 0.06;
          badass.play();

          ProgressTime(badass);

          easterEgg.addEventListener('click', e => {
            badass.pause();
            badass.currentTime = 0;
            audioProgress.className = 'progress-circle p0';

            setTimeout(() => {
              Fuzil.classList.remove('show');
              Fuzil.classList.add('hide');
            }, 6000);
          });

      }

      Fuzil.classList.remove('show');
      Fuzil.classList.add('hide');

    });

  }

  PlayAGame(e){
    let modalGame = document.querySelector('.modal-game');
    if(modalGame != undefined){
      modalGame.classList.add('active');
      modalGame.classList.remove('hidden');
    }
  }


  @HostListener("document:scroll")
  scrollThis(){
      let imageInside = document.querySelectorAll('.hide-face img');
      let audioScroll = document.querySelector('.fixed-index');
      let pSgame = document.querySelector('.presents-a-game');

      window.scrollY > 150 ? audioScroll.classList.add('scrolled') : audioScroll.classList.remove('scrolled');
    

      if(window.scrollY > 360){
        imageInside.forEach(imgS => {
          imgS.classList.add('scrolled');
        });

        setTimeout(() => {
          pSgame.classList.add('scrolled');
        }, 1200);

      }else if (window.scrollY < 150) {
        imageInside.forEach(imgS => {
          imgS.classList.remove('scrolled');
        });

        pSgame.classList.remove('scrolled');
      }
  } 

}
